body{
  padding: 0px;
  margin: 0px;
  font-family: Arial, Helvetica, sans-serif;
}

body a:hover{
  color: orange;
  text-decoration: none;
}

.firstheading h1{
  font-size: 400% !important;
  color: orange;
  position: absolute;
  line-height: 1;
  z-index: -1;
  left: 0px;
  right: 0px;
  margin-top: -20px;
}

hr{
  background-color: orange;
  height: 5px;
  width: 15%;
}

/* For Carousel */
.carousel-item{
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
  
.one{
  background-image: url("image/image_6.jpg");
}
  
.two{
  background-image: url("image/lunch2.jpg");
}

.three{
  background-image: url("image/gallery1.jpg");
}

.four{
  background-image: url("image/myteam.jpg");
}

/* For Services */ 
#services{
  text-align: center;
}

#services .card{
  display: inline-block;
  text-align: center;
  border: 0px none white !important;
}

#services .card img{
  border-radius: 50%;
  color: white;
  background-color: orange;
  width: 130px;
  height: 130px;
}

/* For Menu */ 
#menu{
  text-align: center;
}

.fullmenu{
  width: 24%;
  height: 120px;
  background-color: orange;
  text-align: center;
  display: inline-block;
  margin-left: 1%;
}

#menu .card{
  height: 250px;
  margin: 5px 0 0 2.5%;
  border: 0px white;
}

.fullmenu a{
  color: black !important;
}

#menu .card img{
  border-radius: 50%;
}

.bgpic{
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 50%;
  width: 90%;
  height: 90px;
  margin: auto; 
}

#dinner{
  background-image: url("image/dinner.jpg");
}

#fastfood{
  background-image: url("image/fastfood.jpg");
}

#drink{
  background-image: url("image/drink.jpg");
}

#icecream{
  background-image: url("image/icecreame.jpg");
}

/* For Team */ 
#team{
  text-align: center;
}

.teamtext img{
  width: 200px;
  height: 200px;
  border-radius: 50%;
}

.teamtext{      
  display: inline-block;
  text-align: center;
}

/* For Contact */ 
#contact{
  text-align: center;
}

.contactUs{
  text-align: center;
  margin: auto;
}

#mysubmit{
  background-color: orange;
}

/* For Footer */
#footer{
  text-align: center;
  background-color: black;
} 

#footer .card{
  display: inline-block;
  text-align: center;
  border: 0px none white !important;
  background-color: black;
  color: white;
}

#rights{
  text-align: center;
  color: white;
}

#footer img{
  border-radius: 50%;
  margin-left: 10px;
}

/* Extra large devices (large laptops and desktops, 1201px and up) */
@media only screen and (min-width: 1201px) {
/* For Header */ 
header{
  height: 85px;
  z-index: 100;
  background: radial-gradient(black, transparent);
  overflow: hidden;
}

#logo{
  float: left;
  width: 20%;
  text-align: center;
}

#list ul{
float: right;
}

#list ul li{
  display: inline-block;
  margin: 5px 70px 0 0;
  font-size: 20px;
  text-align: center;
}

header a{
  text-decoration: none;
  color: white;
}

/* For Carousel */
.carousel-caption{
  font-size: 200% !important;
}

.carousel-caption h1{
  font-size: 200% !important;
}
  
/* For About */  
#about{
  width: 70%;
  text-align: center;
  margin: 70px auto 0px auto;
}

.secondheading h1{
  font-size: 300% !important;
  margin-bottom: 20px;
}

.abouttext{
  display: inline-block;
  margin: 0 50px;
}

/* For Services */  
#services{
  margin: 70px auto 0px auto;
}

#services .card{
  width: 30%;
  margin-left: 2.5%;
}

/* For Menu */ 
