  margin: 70px auto 0px auto;
}

#menu .card{
  width: 30%;
  display: inline-block;
}

/* For Team */ 
#team{
  margin: 70px auto 0px auto;
}

.teamtext{      
  width: 30%;
  margin-left: 2.5%;
}

/* For Contact */ 
